import React from './react'
import ReactDOM from './react-dom'
function tick() {
  const element = (
    <div>
      <h1>Hello World</h1>
      <h2>It is { new Date().toLocaleTimeString() }.</h2>
    </div>
  )
  
  // 为什么只用到ReactDOM 在react中还是要先引入react
  ReactDOM.render(
    element,
    document.getElementById('root')
  )
}

setInterval(tick, 1000)

